/*
Как использовать:

<label class="u-radio">
  <input name="my-radio" type="radio">
  <div class="mark"></div>
  <div class="">Моя радиокнопка 1</div>
</label>

*/
.u-radio {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 5px 0px;
  font-size: 10px;
  line-height: 10px;
  text-transform: uppercase;
  color: #252e3a; }
  .u-radio input[type=radio] {
    display: none; }
  .u-radio .mark {
    height: 18px;
    width: 18px;
    border-radius: 50%;
    margin-right: 7px;
    box-sizing: border-box;
    background-color: #FFF;
    border: 1px solid #252e3a;
    transition: border .2s ease-in-out; }
  .u-radio input[type=radio]:checked + .mark {
    border: 4px solid #252e3a; }

/*
Как использовать:

<label class="u-radio-image">
  <input name="my-radio" type="radio">
  <div class="mark" style="background-image:url('image.png')"></div>
  <div class="">Моя радиокнопка 1</div>
</label>

*/
.u-radio-image {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  margin: 5px 0px;
  width: 70px;
  font-weight: 300;
  font-size: 11px;
  line-height: 14px;
  color: #252e3a;
  text-align: center; }
  .u-radio-image input[type=radio] {
    display: none; }
  .u-radio-image .mark {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 70px;
    width: 70px;
    border-radius: 50%;
    box-sizing: border-box;
    border: 1px solid #252e3a;
    transition: border .2s ease-in-out;
    margin-bottom: 7px; }
  .u-radio-image input[type=radio]:checked + .mark {
    border: 5px solid #252e3a; }

/*
Как использовать:

<label class="u-checkbox">
  <input name="my-radio" type="checkbox">
  <div class="mark"></div>
  <div class="">Мой чекбокс 1</div>
</label>

*/
.u-checkbox {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 5px 0px;
  font-size: 10px;
  line-height: 10px;
  text-transform: uppercase;
  color: #252e3a; }
  .u-checkbox input[type=checkbox] {
    display: none; }
  .u-checkbox .mark {
    height: 18px;
    width: 18px;
    margin-right: 7px;
    box-sizing: border-box;
    background-color: #FFF;
    border: 1px solid #252e3a; }
  .u-checkbox .mark:after {
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M11.3996 0.799988L3.97461 9.19999L0.599609 5.38181' stroke='%23252A3A' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0;
    transform: scale(1.2);
    transition: transform .2s ease-in-out,  opacity .2s ease-in-out; }
  .u-checkbox input[type=checkbox]:checked + .mark:after {
    opacity: 1;
    transform: scale(1); }

